.boldNumber {
  font-weight: 800;
  color: rgb(7, 5, 5);
}

p.payout span {
  color: red;
  font-weight: 700;
}

p.revenue span {
  color: blue;
  font-weight: 700;
}

.bill-info {
  padding: 10px;
  border: 1px dashed beige;
  max-width: 80%;
  margin: 0 auto;
  background-color: aliceblue;
}

.bill-info p {
  margin-top: 5px;
}

.billtable-wrapper {
  max-width: 80%;
  margin: 0 auto;
}

.bill {
  margin: 10px auto;
  border: 1px solid black;
}

.bill thead {
  background-color: bisque;
}

.bill > tr {
  border: 1px solid black;
}

.bill thead tr th,
.bill tbody tr td {
  padding: 5px 10px;
}

.bill tbody tr:nth-child(even) {
  background-color: aliceblue;
}
